

body {
  background-color: rgb(235, 235, 235); 

}

header {
  max-width: 767px;
  width: 100%;
  padding: 15px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  height: 60px;
  align-items: center;
  position: sticky;
  top: 0;
  left: 0;
  z-index: 9999;
  background-color: rgb(235, 235, 235);

  img {
    height: 35px;
  }
  p {
    font-size: 18px;
  }
  figure {
    img {
      height: 35px;
    }
  }
}

.main {
  max-width: 767px;
  width: calc(100% - 30px);
  margin: 0 auto;
  margin-top: 20px;
  position: relative;
  .main_img {
    img {
    width: 100%;
    border-radius: 15px;
  }
  h3 {
    position: absolute;
    top: 10%;
    left: 5%;
    line-height: 1.2;
    color: #fff;
    font-size: 25px;
    font-weight: 500;
  }
  } /* .main_img end */

  .search {
    margin: 30px 0;
    position: relative;
    input[type="text"] {
      width: 100%;
      height: 60px;
      border-radius: 40px;

      border: 0 solid #333;
      padding: 0 20px;
      box-shadow: 3px 3px 2px #cacaca;
    }

    input::placeholder {
      font-size: 14px;
    }

    img {
      position: absolute;
      top: 50%;
      right: 20px;
      height: 35px;
      transform: translateY(-50%);
    }

    input:focus {
      outline: 0;
    }
  } /* search end */
  
} /* .main end */

.tour_list {
  max-width: 767px;
  width: calc(100% - 30px);
  margin: 0 auto;
  margin-top: 30px;

  .item {
    background-color: rgb(255, 255, 255);
    border-radius: 15px;
    box-shadow: 4px 4px 4px 2px #cacaca;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    width: 100%;
    height: 250px;
    .item_img {
      position: relative;
      width: 60%;
      figure:nth-child(1) {
       img {
        width: 100%;
        height: 250px;
        object-fit: cover;
        border-radius: 15px;
    } /* img end */
    }
    figure:nth-child(2) {
      position: absolute;
      top: 10px;
      right: 10px;
      opacity: 80%;
      img {
        height: 35px;
      }
    }
    } /* .item_img end */
     
    .item_text {
      height: 250px;
      width: 50%;
      padding: 30px;
      position: relative;
      p {
        font-size: 20px;
        font-weight: 500;
        margin-bottom: 10px;
        word-break: keep-all;
      }
      span {
        word-break: keep-all;
        line-height: 1.3;
        color: #666;
        font-size: 16px;
      }
      figure {
        width: 100px;
        margin-top: 10px;
        
        img {
          width: 100%;
          height: 100%;
        }
      }
      strong {
        font-weight: normal;
        background-color: #FFDB5B;
        border-radius: 20px;
        display: block;
        width: 90px;
        height: 30px;
        text-align: center;
        line-height: 30px;
        font-size: 16px;
        position: absolute;
        bottom: 30px;
      }

    } /* .item_text end */
  } /* item end */
 
}

.nav_bottom {
  max-width: 767px;
  width: calc(100% - 30px);
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding: 0 40px;
  gap: 40px;
  align-items: center;
  background-color: #FFDB5B;

  height: 70px;
  border-radius: 30px;

  position: sticky;
  left: 0;
  bottom: 20px;
  z-index: 9999;
  li {
    img {
      height: 25px;
    }
  }
}